  'story.scene.sequel1': '<h3>A folytats</h3><h4>Brminem hasonlsg valsgos szemlyekkel csak a vletlen mve...</h4><p>A mese elemzse sorn olykor vgletesen eltvolodunk az eredeti trtnettl, majd onnan visszatekintve szemllhetjk a trtnetet egy j nzpontbl. Ebben a grafikonos rszben humorosnak sznt stlusban els gondolataimat vetettem le.</p><img src="images/snowwhite/problems/14030ffbdff1b5a0761ba0b1c5af82d5.jpg" class="ui small left floated image" /><p>Nos, a mese vget rt, de egy msik elkezddtt. Hfehrke hossz veken t lt a herceg (a frje) mellett, fztt, mosott, takartott, s ezrt cserbe... nem sok mindent kapott. Mr nem szltottk hfehrknek, noha kirlyn volt, meg egyben felesg, a jegyz Karsain Horvth Gizella nevet jegyezte be a hzassgi anyaknyvbe, ahogy az lenni szokott. A hossz vek alatt elvesztette bre hfehr jellegt, plusz az utazsok egzotikus orszgokba, meg az a rengeteg nikotin a cigibl... A tndrmese lassan-lassan szociodrmv alakult. s egy nap jtt az igazi trauma:</p><p>egy msik kirlysgban megszletett Hfehrke</p>',

