  'story.scene.sequel4': '<h3>A vadsz</h3><p>A vadszrl nem sokat mond a mese. Egyetlen feladata, hogy a Gonosz kirlyn parancsra lje meg hfehrkt s hozza el annak kivgott szvt bizonytkul. Nos egy szvrl nehz brkit is azonostani, gy ez is szimblikus: trje ssze a szvt. Vegye el szpsge forrst, a nav, romlatlan szeretetet, lelke bels fnyt, amely a szpsgnek valdi forrsa.</p><p>Hogyan trje ssze a szvt? jjel vigye ki az erdbe, s ott... vegye el a fegyvert, s dfje le, avagy clozzon s ljn. Aztn vegye el a szvt, s tegye a gonosz el bizonytkul.</p><p>A vadsz teht nem affle vadgazdlkod szerepben jelenik meg, hanem inkbb olyanban aki msra vadszik, gy is mondhatnnk, p!#@vadsz, hogyha nagyon egyrtelmen be akarjuk sorolni... gy mindjrt jobban sszell a kp:</p>',

  'story.scene.sequel5': '<p>Figyeljk meg, hogy kialakult 3 pr. Innen mr nagyjbl minden egyenesen kvetkezik majd. Kzpen a Hfehrke mese fszerepli, jobbra s balra msik 2 mese fszerepli. Balrl fenn a j karakterek, akik segtenek, jobbrl lenn az ellenlbasok.</p><h3>Hrman prban?</h3><p>Jogosan lehetne mondani, hogy a mese nem gy van, a kirly ott egyedl van a kirlyn halla okn, a vadsz s a gonosz mostoha kztt pedig mg csak lettrsi kapcsolat sincs... Vlemnyem szerint a karakterek kibontsa mgis erre az eredmnyre vezet, a szerepeket gy lehet prostani.</p><p>A vadsz s a gonosz kirlyn kapcsolatra egyik legjobb bizonytk, hogy a mese, mint egy jobb hollywoodi film, direkt nem tesz emltst a kztk lv kapcsolatrl, hogy ne is sejtsk, ezltal nvelve a felismers megdbbent erejt  ha eljn az ideje. Annyit tudni, hogy a gonosz tettben egyttmkds kezddik, de a vadsz nem engedelmeskedik, ezrt a gonosz kirlyn brtnbe zrja.</p><p>A felttelezs szerint a gonosz mostoha maga egykor Hfehrke volt. Akkor ht volt neki frje, a herceg, de frj nlkl rkezik a trtnetbe...</p>',

  'story.scene.sequel6': '<h3>A vls</h3><p>Tiszta sor, Hfehrke s a herceg elvltak. gy mg rthetbb mirt olyan gonosz a mostoha. Hogy  akart-e vlni, vagy a herceg, azt nem tudni. Mindkettjknek volt oka r bven.</p><p>A herceg nem volt egy egyszer eset... Eleinte odaad volt, a csillagokat is odagrte, de vgl maradt egy laktelepi laks. A htvgket eleinte egytt tltttk, majd a herceg szvesebben srztt a bartaival, s nha sokig elmaradt... Mr nem volt aki dcsrje Hfehrke csods jellemvonsait, melyeket mind jl ismernk, mert amikor egytt vacsorztak, a herceg csak bmult maga el a tnyrba, s ha Hfehrke krdezte valamirl, a vlasz ltalban az volt, most szeretnk nyugodtan enni, ha nem problma. Aztn volt, hogy tbb pnzt vitt, mint amennyit hozott, iszkossg, szerencsejtk, tovbbi kimaradsok...</p>',

  'story.scene.sequel7': '<h3>A herceg transzformcija</h3><img src="images/snowwhite/girlfriends/8fb991217a3562df7e0750a697eae6d5.jpg" class="ui medium left floated image" /><p>De Hfehrke sem volt egy egyszer eset... Az csak termszetes, hogy neki kellet lenni a kzppontban, s a f tma ezzel kellett hogy kapcsolatos legyen: ruhk, pipere, udvarhlgyek, csupa olyan tma ami a herceget hidegen hagyta.</p><p>Amikor a herceg a bartaival szeretett volna tallkozni, Hfehrke nem repesett az rmtl, s ez a hercegben lelkifurdalst bressztett. Bnatoss tette, hogy  nem tudja gy elszrakoztatni arjt, mint pl. a tbbi herceg a legutbbi zens estn, akik bezzeg felsoroltk Hfehrke minden bjt... Enyhe depreszijban, mikor egy kemnyen vgigdolgozott nap utn kedvese a vacsornl krdre vonta, mr azon merengett, hogy valban ez-e az a Hfehrke, akit neki a mesben megrt mdon felesgl kellett vennie. Mivel nem sikerlt meggyznie magt, s ebben Hfehrke sem volt segtsgre, bnatban, hogy mekkora egy hlye, elvesztette a kontrollt letmdja felett, italba folytotta bnatt, Hfehrke mrhetetlen kltekezst szerencsejtkbl prblta fedezni, s...</p>',

