  'story.scene.sequel10': '<h3>A ltoms, vagy dezsav?</h3><p>A kirlynvel kezddik a mese, de nem sokat tudunk meg rla. Szeretnival, szp, egyetlen hibja csak hogy meghal. De ez is csak szimblum, suprise-suprise: a kirlyn nem hal meg! Felldozza nmagt, szerepbe temetkezik, ezrt a tetszhalott llapot. Egyetlen szerepe pedig: ltrehozni Hfehrkt</p><p>A kirlyn elbrndozik, avagy ksbb lmot lt. Megfogalmazdik benne leend gyemeke tbb tulajdonga: Hajszn, brszn, ajkainak szne. Genetikailag azonban csak olyan tulajdonsgok rklhetek, amelyet az egyn  valamilyen formban -  maga is birtokol. A kirlyn  felteheten maga is rendelkezett a hfehrkre jellemz tulajdonsgokkal, elkpzelhet, hogy azokat sorolta el, melyeket maga is kedvelt  egykori nmagban, amikor mg !  is Hfehrke volt!!!</p>',

