  'story.scene.sequel14': '<h3>A szv</h3><p>Milyen volt a kirlyn? - Szivlyes. Milyen volt a mostoha? - Szvtelen. Mit akart tenni Hfehrkvel? - Szvtelenttetni akarta.</p><h3>A szvtelen</h3><p>Mirt volt szvtelen a mostoha? - Valsznleg, mikor mg  is Hfehrke volt, a vadsz t is kicsalta az erdbe, elvette fegyvert, s... ezt a szv bnta. Az is lehet, hogy tovbbi vadszok is megprbltk megszerezni a szvt, mire mr semmi sem maradt... St, lehet, hogy arra a kvetkezretsre jutott, hogy ez gy normlis, s vgl maga csalta ki a vadszokat az erdbe. De ez nem tette boldogg. A szpsgrt sok mindenre kpes egy n, mg sebszeti beavatkozsokra is, nos,  szvleszvst csinltatott magn, remlve, hogy sikeresebb lesz a frfiaknl, s valban, a mese nem emlti hogy csf lett volna...</p><h3>Az reg kirly</h3><p>A kirly mg kimaradt, szerepvllalsa, jellemzi megkrdjelezhetek. Hfehrke szeret apjaknt, a kirlyn zvegyeknt szeretnival, de amikor sszell a gonosz mostohval, s hagyja, hogy  az Hfehrke ellen keljen, sznalmas ldozatt, tutyimutyi papucsfrjj vlik. Ki  valjban s honnan szrmazik?</p>',

