        <p>Lrvallapot: Hfehrke, herceg. Ni t, frfi t  egyedi t, de megfeleltethet. Felist-leist  minden llapotbl el lehet mozdulni, mindegyiknek vannak jellemzi. A mese kisarktja a karakterek megtlst, de egyben rvilgt, nincs abszolt j s rossz, ez nzpont, viszony krdse. A viszonylatok hatrozzk meg a lehetsges elmozdulsi irnyokat. Horizontlis, vertiklis utak, szemlyisgfejlds. Minden mesben vannak utak, melyeket a hsnek be kell jrnia, s ezek az utak mindig a jellemfejlds bels tjait szimbolizljk.</p>
