        <p>Vizsgljuk meg az erdt alkot egysget: a ft. A fa csodlatosan szp szimblum: 3 alapveten elklnl rsze van:</p>
        <ol>
          <li>Koronja: akr csak a kirlyoknak. Sztterl az gben, a fnybe kvnkozik</li>
