        <p>A folyamat, melyet fotoszintzis nven tantanak nem ms, mint ahogyan a nvny az isteni fny energijt elnyeli, megnemesti az anyagot, hogy sajt testbe tudja pteni. Folyamatosan cirkulltatja a fels s als vilg sszetevit, a fny energijval tr a stt fldbe gykereivel az anyagrt, s a stt anyagbl pti koronjt, hogy fnyhez jusson. (Shiva - Shakti, Kundalini)</p>

        <blockquote>
          <p><b>A fa jelkpezi a fggleges tagoltsg vilgot, sszekti annak hrom szintjt. Gykereivel az alvilgba, koronjval az gbe nylik.</b> Gykerei kzt csszmszk, frgek tanyznak, koronjban madarak, gitestek lnek. Kzpen, a kt ellenttes plus hatrn helyezkedik el az emberek vilga. A smnok s a npmesk hsei innen indulnak tnak az gig r fn, hogy elrjk a kegyes vagy rt szellemek jindulatt, kiszabadtsk az elrabolt kirlylnyt.</p>
